@import './nullStyle.css';

body {
    font-size: 20px;
    height: 100%;
}

.wrapper {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    min-height: 100vh;
    -ms-flex-pack: justify;
        justify-content: space-between;
    overflow: hidden;
    background: -webkit-gradient(linear, left bottom, left top, from(#91EAE4), color-stop(#86A8E7), to(#7F7FD5)) ;
    background: -webkit-linear-gradient(bottom, #91EAE4, #86A8E7, #7F7FD5) ;
    background: -o-linear-gradient(bottom, #91EAE4, #86A8E7, #7F7FD5) ;
    background: linear-gradient(to top, #91EAE4, #86A8E7, #7F7FD5) ;

}



